import React, { useCallback, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'


export default function Hamburger() {
  const navigate = useNavigate()

  const handleNavigate = (path) => {
    navigate(path);
    document.body.style.overflow = 'auto';
    window.scrollTo(0, 0);
  };

  const handleLogout = () => {
    sessionStorage.clear();
    window.location.reload();
  };
  useEffect(() => {
    return () => {
      document.body.style.overflow = 'auto';
    };
  }, []);

  return (
    <>
      <div className='rounded-circle d-flex justify-content-center' style={{background: "white", borderRadius: "60px", height: "50px", width: "50px"}}>
      <i className="fa-solid fa-bars  align-items-center justify-content-end align-self-center" style={{ color: "black", fontSize: "25px", cursor: 'pointer'}} data-bs-toggle="offcanvas" data-bs-target="#offcanvasExample" aria-controls="offcanvasExample"></i>
      </div>  
      <div className="offcanvas offcanvas-end d-flex" style={{width: "80%", height: "auto", zIndex: "10000", background: "rgba(53, 53, 53, 1)"}} tabIndex="-1" id="offcanvasExample" aria-labelledby="offcanvasExampleLabel"  data-bs-no-jquery  data-bs-backdrop="true" data-bs-scroll="false">
        <div className="offcanvas-header pt-5 pb-4 px-3" style={{justifyContent: "start"}}>
          <a className="navbar-brand mx-md-5 py-0 mr-auto" style={{fontWeight: "800", width: "75%"}} href="/">
            <img style={{width: "100%"}} src={process.env.PUBLIC_URL + "/images/LOGO.svg"} alt=""></img>
          </a>
          <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close" style={{color: "white"}}></button>
        </div>
        <div className="offcanvas-body d-flex flex-column pt-4">
          <div className='p-0 px-1 mb-auto'>
            <div className='text-white mb-3 py-2 px-3' style={{border: "2px solid rgba(255, 255, 255, 0.03)", background: "transparent", borderRadius: "78px"}} onClick={()=>handleNavigate("/")}>
              <div className='d-flex my-1 align-items-center'>
              <div className='p-2' style={{background: "white", borderRadius: "50%", width: "40px", height: "40px", display: "flex", alignItems: "center", justifyContent: "center"}}>
                <i class="fa-solid fa-house" style={{color: "black"}}></i>
              </div>
              <p className='mb-0 ml-3' style={{fontSize: "18px"}}>Home</p>
              </div>
            </div>
            <div className='text-white mb-3 py-2 px-3' style={{border: "2px solid rgba(255, 255, 255, 0.03)", background: "transparent", borderRadius: "78px"}} onClick={()=>handleNavigate("/events")}>
              <div className='d-flex my-1 align-items-center'>
              <div className='p-2' style={{background: "white", borderRadius: "50%", width: "40px", height: "40px", display: "flex", alignItems: "center", justifyContent: "center"}}>
              <i class="bi bi-dice-6-fill" style={{color: "black"}}></i>
              </div>
              <p className='mb-0 ml-3' style={{fontSize: "18px"}}>Events</p>
              </div>
            </div>
            <div className='text-white mb-3 py-2 px-3' style={{border: "2px solid rgba(255, 255, 255, 0.03)", background: "transparent", borderRadius: "78px"}} onClick={()=>handleNavigate("/contact-us")}>
              <div className='d-flex my-1 align-items-center'>
              <div className='p-2' style={{background: "white", borderRadius: "50%", width: "40px", height: "40px", display: "flex", alignItems: "center", justifyContent: "center"}}>
              <i class="fa-solid fa-circle-question" style={{color: "black"}}></i>
              </div>
              <p className='mb-0 ml-3' style={{fontSize: "18px"}}>Help</p>
              </div>
            </div>
          </div>
          <div className='p-0 px-1 mt-auto'>
            <div className='text-white mb-3 py-2' style={{border: "2px solid white", background: "transparent", borderRadius: "78px"}} onClick={()=>handleNavigate("/contact-us")}>
              <div className='d-flex my-1'>
                <p className='mb-0 mx-auto' style={{fontSize: "18px"}}>Sell</p>
              </div>
            </div>
            <div className='mb-3 py-2' style={{border: "2px solid rgba(255, 255, 255, 0.03)", background: "white", borderRadius: "78px", color: "black", fontWeight: "700"}}  onClick={()=>handleNavigate("/login")}>
              <div className='d-flex my-1'>
                <p className='mb-0 mx-auto' style={{fontSize: "18px"}}>Sign In</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
